import React, { Component } from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import routes from "../routes";
import Collapsible from "react-collapsible";

const Wrap = styled.nav`
  width: 200px;
`;

const SectionItem = styled(Collapsible)``;
const SectionPageItem = styled.div`
  a {
    padding-left: 10px;
  }
`;

const SectionLinkStyled = styled.a`
  cursor: pointer;
  display: inline-block;
  width: 100%;
  letter-spacing: 1px;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  font-style: normal;
  line-height: 1em;
  color: #ffffffb3;
  padding: 1em 1em 1em 0;
  transition: color 0.2s ease-out;
`;

const NavLinkStyled = styled(NavLink)`
  display: inline-block;
  width: 100%;
  letter-spacing: 1px;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  font-style: normal;
  line-height: 1em;
  color: #ffffffb3;
  padding: 1em 1em 1em 0;
  transition: color 0.2s ease-out;

  &:active,
  &:hover,
  &.active {
    color: white;
  }
`;
function renderSectionItem(section, props) {
  console.info(props);
  return (
    <SectionItem
      transitionTime={250}
      open={props.mobile}
      easing="ease-in-out"
      trigger={<SectionLinkStyled>{section.label}</SectionLinkStyled>}
    >
      {section.pages.map(({ to, label }) => (
        <SectionPageItem key={section.to + to}>
          <NavLinkStyled to={section.to + to}>{label}</NavLinkStyled>
        </SectionPageItem>
      ))}
    </SectionItem>
  );
}

function renderNavItem({ to, label }) {
  return (
    <NavLinkStyled to={to} exact={to === "/"}>
      {label}
    </NavLinkStyled>
  );
}

export class Nav extends Component {
  render() {
    return (
      <Wrap {...this.props}>
        {routes.map(
          props =>
            props.pages
              ? renderSectionItem(props, this.props)
              : renderNavItem(props),
        )}
      </Wrap>
    );
  }
}

export default Nav;



