import React, { Component } from "react";
import ReactMarkdown from "react-markdown";
import { files } from "../core/markdown";

class About extends Component {
  render() {
    return (
      <div>
        <ReactMarkdown source={files.get("about.md")} />
      </div>
    );
  }
}

export default About;



