import React, { Component } from "react";
// import ReactMarkdown from "react-markdown";
// import { files } from "../core/markdown";

class Work extends Component {
  render() {
    const { params } = this.props.match;
    return (
      <div>
        <h1>Work #{params.section}</h1>
      </div>
    );
  }
}

export default Work;



